import { Component } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],


  
})
export class AppComponent {
  title = 'Typo';
  constructor(private _http:HttpClient) {
    this.c1.name = "eli"
  }

  c1:Cust = new Cust();
  click1(){
    this.getBooks().subscribe(b => this.c1.name = b.toString())
  }
  click2(){
    this.getAllBooks().subscribe(b => this.c1 = b)
  }
  click3(x){
    this.getString(x.toString()).subscribe(b => this.c1.name = b.toString())
  }

  getAllBooks()
  {
    return this._http
        .get<Cust>("./getcust") // GET request
  }
  getBooks()
  {
    return this._http
        .post("./apitest/",'20') // POST request with argument
  }
  getString(x)
  {
    let y: string = '"' + x.toString() + '"'
    console.log(y);
    return this._http
        .post("./apistring/", y)
  }
  keyupName(event: any) {
    var element: HTMLInputElement = event.target; // Lấy được phần tử DOM
    var val: string = element.value;              // Đọc các thuộc tính phần tử
    //this.c1.name = val;
    this.getString(val.toString()).subscribe(b => this.c1.name = b.toString());
  }
}

export class Cust{
  name:string;
  age:number;
  city:string;
}